import React from "react"
import {connect} from "react-redux"
import { bindActionCreators } from "redux";
import {IProps} from "@public/common/interface"
import { Button, Col, Descriptions, Popconfirm, Row, Table } from "antd";
import { parkLeaseColumns } from "../../columns";
import {getParkLease, changeParkLease, changeLotParkLease } from "@plate/actions/parkAction"
import SearchModular from "@public/components/Modular/SearchModular";
import StatusElement from "@public/components/Element/StatusElement";
import AddModular from "@public/components/Modular/AddModular";
import ParkLot from "./parklot";
import LeaseActive from "./leaseactive"


interface Props extends IProps {
  parklease:any;
}

let params = {
  current: 1,
  licenseLinkName: "",
  licenseLinkPhone: "",
  license: "",
  state: "",
  status: "",
}
let resetParams = {
  current: 1,
  licenseLinkName: "",
  licenseLinkPhone: "",
  license: "",
  state: "",
  status: "",
}

class ParkLeaseconf extends React.Component<Props> {

  state = {
    addVisible: false,
    addLotVisible: false,
    editVisible: false,
    detail: {id: ""}
  }

  componentDidMount(){
    this.initial(params)
  }

  initial(params:any, obj={}){
    this.props.actions.getParkLease({...params, parkId: this.props.match.params.id}, obj)
  }

  getCol(){
    return [...parkLeaseColumns, {
      title: "",
      dataIndex: "status",
      render:(item:any, rows:any)=><StatusElement notAll size="small" value={item} onChange={()=>{
        this.props.actions.changeParkLease({
          objective:"enable",
          id: rows.id
        }, (res:any)=>{
          this.props.utils.OpenNotification("success")
          this.initial(params, {obj:res, type:"edit"})
        })
      }} />
    }, {
      title: "",
      render:(item:any)=>{
        return (
          <>
            <Button size="small" type="link" onClick={()=>this.setState({editVisible: true, detail: item})} ></Button>
            <Button size="small" type="link"></Button>
            <Popconfirm title="" onConfirm={()=>{
              this.props.actions.changeParkLease({
                objective:"remove",
                id: item.id
              }, (res:any)=>{
                this.props.utils.OpenNotification("success")
                this.initial(params, {refresh: true})
              })
            }}>
              <Button size="small" type="link"></Button>
            </Popconfirm>
          </>
        )
      }
    }]
  }

  render() {
    const {spinning, utils, parklease} = this.props
    const {addVisible, editVisible, detail, addLotVisible } = this.state

    return (
      <>
        <LeaseActive {...this.props} />
        <Row>
          <Col span={20}>
            {parklease?<Descriptions column={6} style={{background: "#fff", padding: "10px 0 0 10px "}} >
              <Descriptions.Item label="">{parklease.map.seatSum}</Descriptions.Item>
              <Descriptions.Item label="">{parklease.map.waitSeat}</Descriptions.Item>
              <Descriptions.Item label="">{parklease.map.longSeat}</Descriptions.Item>
              <Descriptions.Item label="">{parklease.map.temporarySeat}</Descriptions.Item>
            </Descriptions>:null}
            <Table size="small" columns={this.getCol()} dataSource={parklease?utils.addIndex(parklease.list):[]} 
              pagination={utils.Pagination(parklease, page=>{
                params.current = page
                this.initial(params)
              })} />
          </Col>
          <Col span={4}>
            <SearchModular
              before={(
                <>
                  <Button type="primary" onClick={()=>this.setState({addVisible: true})} ></Button>
                  <Button className="mgl10" type="primary" ghost onClick={()=>this.setState({addLotVisible: true})} ></Button>
                </>
              )}
              layout="vertical"
              initialValues={params}
              resetValues={resetParams}
              submitSearch={(values:any)=>{
                params = values?{...params, ...values}:resetParams
                this.initial(params)
              }}
              data={[
                {label: "", name: "seatCod", type: "input"},
                {label: "", name: "licenseLinkName", type: "input"},
                {label: "", name: "licenseLinkPhone", type: "input"},
                {label: "", name: "license", type: "input"},
                {label: "", name: "state", type: "select", selectList: [
                  {label: "", id: ""},
                  {label: "", id: 0},
                  {label: "", id: 1},
                  {label: "", id: 2},
                ]},
                {label: "", name: "status", type: "status"},
              ]}
            />
          </Col>
        </Row>

        <AddModular
          title=""
          spinning={spinning}
          visible={addLotVisible}
          onCancel={()=>this.setState({addLotVisible:false})}
          onOk={(values)=>{
            this.props.actions.changeLotParkLease({
              ...values,
              parkId: this.props.match.params.id
            }, ()=>{
              this.props.utils.OpenNotification("success")
              this.initial(params, {refresh: true})
              this.setState({addLotVisible: false})
            })
          }}
          data={[
            {label: "", name: "floorCode", type: "input"},
            {label: "", name: "unitCode", type: "input"},
            {label: "", name: "areaCode", type: "input"},
            {label: "", name: "prefix", type: <ParkLot/>, rules: true},
            {label: "", name: "state", type: "select", selectList: [
              {label: "", id: 0},
              {label: "", id: 1},
              {label: "", id: 2},
            ], rules: true},
            {label: "", name: "status", type: <StatusElement notAll/>, rules: true},
            {label: "", name: "remark", type: "textarea"},
          ]}
        />
        
        <AddModular
          title=""
          spinning={spinning}
          visible={addVisible}
          onCancel={()=>this.setState({addVisible:false})}
          onOk={(values)=>{
            this.props.actions.changeParkLease({
              ...values,
              objective: "add",
              parkId: this.props.match.params.id
            }, ()=>{
              this.props.utils.OpenNotification("success")
              this.initial(params, {refresh: true})
              this.setState({addVisible: false})
            })
          }}
          data={[
            {label: "", name: "floorCode", type: "input"},
            {label: "", name: "unitCode", type: "input"},
            {label: "", name: "areaCode", type: "input"},
            {label: "", name: "seatCode", type: "input", rules: true},
            {label: "", name: "state", type: "select", selectList: [
              {label: "", id: 0},
              {label: "", id: 1},
              {label: "", id: 2},
            ], rules: true},
            {label: "", name: "status", type: <StatusElement notAll/>, rules: true},
            {label: "", name: "remark", type: "textarea"},
          ]}
        />

        <AddModular
          title=""
          spinning={spinning}
          visible={editVisible}
          initialValues={detail}
          onCancel={()=>this.setState({editVisible:false})}
          onOk={(values)=>{
            this.props.actions.changeParkLease({
              ...values,
              objective: "update",
              parkId: this.props.match.params.id,
              id: detail.id
            }, (res:any)=>{
              this.props.utils.OpenNotification("success")
              this.initial(params, {obj: res, type: "edit"})
              this.setState({editVisible: false})
            })
          }}
          data={[
            {label: "", name: "floorCode", type: "input"},
            {label: "", name: "unitCode", type: "input"},
            {label: "", name: "areaCode", type: "input"},
            {label: "", name: "seatCode", type: "input", rules: true},
            {label: "", name: "state", type: "select", selectList: [
              {label: "", id: 0},
              {label: "", id: 1},
              {label: "", id: 2},
            ], rules: true},
            {label: "", name: "status", type: <StatusElement notAll />, rules: true},
            {label: "", name: "remark", type: "textarea"},
          ]}
        />
        
      </>
    );
  }
}

const mapDispatchProps = (dispatch:any)=>{
  return {
    actions: bindActionCreators({getParkLease, changeParkLease, changeLotParkLease }, dispatch)
  }
}

const mapStateProps = (state:any)=>{
  return {
    parklease: state.park.parklease,
    utils: state.app.utils,
    spinning: state.app.spinning
  }
}

export default connect(mapStateProps, mapDispatchProps)(ParkLeaseconf)