import React, { useEffect, useState } from "react"
import {getParkAvailNumber, addOrUpParkConfig, getByParkConf} from "@plate/actions/parkAction"
import {Button, Card, Checkbox, Col, Form, InputNumber, Radio, Row, Select, Image} from "antd"
import { bindActionCreators } from "redux"
import { connect } from "react-redux"
import { UtilsProps } from "@public/common/interface"
import OpenCloseElement from "@plate/components/Element/OpenCloseElement"
import "./index.less"
import BeInputNumber from "@public/components/Element/BeInputNumber"
import { RedoOutlined } from "@ant-design/icons"
import { useHistory, useLocation, useParams, useRouteMatch } from "react-router-dom"

const {Option} = Select

interface Props {
  parkconf:any;
  utils:UtilsProps;
  actions:any;
}

const tMinute = [5,10,15,20,25,30,35,40,45,50,55,60]

const Parkconf:React.FC<Props> = ({
  parkconf,
  utils,
  actions,
})=>{
  const [form] = Form.useForm();
  const useparams:any = useParams();
  const [isAdvancePay, setIsAdvancePay] = useState(0)
  const params:any  = useRouteMatch().params;
  

  const getParkAvailNum = ()=>{
    actions.getParkAvailNumber({parkId: useparams.id}, (res:any)=>{
      form.setFieldsValue({availpark: res})
    })
  }
  
  useEffect(()=>{
    getParkAvailNum()
    if(parkconf){
      form.setFieldsValue(parkconf)
    }
  }, [parkconf])

  


  const onFinish = (values:any)=>{
    actions.addOrUpParkConfig({
      ...values,
      parkId: params.id,
      id: parkconf.id
    }, (res:any)=>{
      utils.OpenNotification("success")
      actions.getByParkConf({parkId: params.id}, {obj:res, type: "obj"})
    })
  }
  
  return (
    <div className="parkconf">
      <Card size="small" title="" extra={<Button type="primary" onClick={()=>form.submit()} ></Button>} >
        <Form
          form={form}
          onFinish={onFinish}
        >
          <Row gutter={20} className="greybg">
            <Col span={6}>
              <Form.Item label="" name="runModel">
                <Select>
                  <Option value={0}></Option>
                  <Option value={1}></Option>
                </Select>
              </Form.Item>
            </Col>
            <Col span={4} >
              <Form.Item name="isa" valuePropName="checked" >
                <Checkbox></Checkbox>
              </Form.Item>
            </Col>
            <Col span={4}>
              <Form.Item name="isb"  valuePropName="checked" >
                <Checkbox></Checkbox>
              </Form.Item>
            </Col>
            <Col span={10}>
              <Form.Item label="" name="isFull">
                <OpenCloseElement/>
              </Form.Item>
            </Col>
          </Row>
          
          <Row gutter={20} className="greybg mgt10">
            <Col span={6}>
              <Form.Item label="" name="parkNumber">
                <InputNumber style={{width: "100%"}}/>
              </Form.Item>
            </Col>
            <Col span={6}>
              <Form.Item label="" name="availpark">
                <BeInputNumber readOnly suffix={<RedoOutlined style={{cursor:"pointer", color: "#13c2c2", fontSize:18}}
                onClick={getParkAvailNum} />} />
              </Form.Item>
            </Col>
            <Col span={10}>
              <Form.Item label="" name="isOpen">
              <Radio.Group  >
                <Radio value={1}></Radio>
                <Radio value={0}></Radio>
              </Radio.Group>
              </Form.Item>
            </Col>
          </Row>
          <Row gutter={20} className="mgt10">
          {parkconf && parkconf.isSon==0?<Col span={12} className="greybg">
              <>
                <Form.Item label="" name="isVisitor" labelCol={{span: 12}} >
                  <OpenCloseElement/>
                </Form.Item>
                <Form.Item label="" name="isFence" labelCol={{span: 12}}>
                  <OpenCloseElement/>
                </Form.Item>
                <Form.Item label="" name="isLoopCost" labelCol={{span: 12}}>
                  <OpenCloseElement/>
                </Form.Item>
                <Form.Item label="" name="isAdvancePay" labelCol={{span: 12}} 
                getValueFromEvent={(v)=>{
                  setIsAdvancePay(v)
                }} >
                  <OpenCloseElement/>
                </Form.Item>
                { form.getFieldValue("isAdvancePay") == 1 || isAdvancePay==1?
                <Form.Item label="" name="advanceTime" labelCol={{span: 12}}>
                  <Select>
                    {tMinute.map(item=>(
                      <Option key={item} value={item}>{item+""}</Option>
                    ))}
                  </Select>
                </Form.Item>:null}
                <Form.Item label="" name="isBindOwner" labelCol={{span: 12}}>
                  <OpenCloseElement/>
                </Form.Item>
                <Form.Item label="" name="isDriverPerfect" labelCol={{span: 12}}>
                  <OpenCloseElement/>
                </Form.Item>
                <Form.Item label="" name="isTravelPerfect" labelCol={{span: 12}}>
                  <OpenCloseElement/>
                </Form.Item>
                <Form.Item label="" name="isCardPerfect" labelCol={{span: 12}}>
                  <OpenCloseElement/>
                </Form.Item>
              </>
            </Col>:<Col span={12}></Col>}
            <Col span={12}>
              <Image/>
            </Col>
          </Row>
        </Form>
      </Card>
    </div>
  )
}


const mapDispatchProps = (dispatch:any)=>{
  return {
    actions: bindActionCreators({getParkAvailNumber, addOrUpParkConfig, getByParkConf},dispatch)
  }
}

const mapStateProps = (state:any)=>{
  return {
    parkconf: state.park.parkconf,
    utils: state.app.utils
  }
}

export default connect(mapStateProps, mapDispatchProps)(Parkconf)